//  sign  & 
function get_sign_src(reqParamJson) {
  var signSrcArray = [];
  for (var r in reqParamJson) {
    if (r != 'sign') {
      signSrcArray.push(r + '=' + reqParamJson[r]);
    }
  }
  console.log("signSrcArray :", signSrcArray);
  //
  signSrcArray.sort();
  var signSrc = '';
  for (var i in signSrcArray) {
    if (i != 0) {
      signSrc += '&';
    }
    signSrc += signSrcArray[i];
  }
  return signSrc;
}

//md5
function md5(type, data) {
  console.log(":" + data);
  switch (type) {
    case '1': //md5
      return global.filemd5(data);
      break;
    case 1: //md5
      return global.filemd5(data);
      break;
    case '2': //md5
      var buf = new buffer(data);
      var str = buf.toString("binary");
      return crypto.createHash("md5").update(str).digest("hex");
      break;
    case 2: //md5
      var buf = new buffer(data);
      var str = buf.toString("binary");
      return crypto.createHash("md5").update(str).digest("hex");
      break;
    default:
      return '';
  }
}

function findcode(arr) {
  let max = 0;
  if (arr !== undefined) {
    if (typeof arr == 'string') {
      arr = arr.split(',');
    }

    arr.forEach((num) => {
      if (!(num === undefined || num === '')) {
        if (parseInt(max) < parseInt(num)) {
          max = num;
        }
      }
    })
  }
  //
  let maxint = Math.ceil(max / 9.5);
  let maxval = maxint * 10; //
  return maxval;
}

