//
function initBar(chart, xAxis, yAxis) {

  var option = {
    tooltip: {
      trigger: 'axis',
      axisPointer: {            // 
        type: 'shadow'        // 'line' | 'shadow'
      },
      backgroundColor: 'rgba(97, 127, 251, 0.8)',
      padding: [5, 20, 5, 10],//   
      confine: true,// tooltip 
      formatter: function (params) {

        let index = params[0].dataIndex;
        let htmlStr = '';
        let like = xAxis[index];
        let take = yAxis[index];
        if (!take) {
          take = 0;
        }
        htmlStr += '' + take + "%" + '\n' + '' + like + '%' + "-" + (parseInt(like) + 10) + '%';
        return htmlStr;
      }
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '15%',
      height: '80%',
      containLabel: true,
    },
    xAxis: [
      //x
      {
        data: yAxis,
        show: false
      },
      {
        data: xAxis,
        axisLabel: {
          interval: 0,
          show: true,
        },
        position: 'bottom',
        boundaryGap: false,
        axisPointer: {
          show: false,
        },
        axisLine: {
          lineStyle: {
            type: 'solid',
            color: '#D2D2D2',//
          }
        },
      },
    ],
    yAxis: {
      type: 'value',
      axisLine: {
        lineStyle: {
          type: 'solid',
          color: '#98B1FF',//
        }
      },
      splitLine: { show: false },//

    },
    series: [{
      type: 'bar',
      data: yAxis,
      itemStyle: {
        normal: {
          color: '#98B1FF',
        }
      },
      barCategoryGap: '0%'
    }],
  }

  chart.setOption(option, true);

}


